import { Fragment, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import { useDispatch, useSelector } from 'react-redux';
import { adminUpdateUserData } from '../../redux/userReducer';
import Switch from "react-switch";
import { userService } from '../../services/users.service';

const AdminEditUserModal = ({isModal, setIsModal, seluser}) => {
  const navigate = useNavigate();
  const dispatch = useDispatch(); 
  const [businessname, setBusinessname] = useState('');
  const [password, setPassword] = useState('');
  const [address, setAddress] = useState('');
  const [postcode, setPostcode] = useState('');
  const [telephone, setTelephone] = useState('');
  const [notes, setNotes] = useState('');
  const [active, setActive] = useState(false);
  const [is_admin, setIsAdmin] = useState(false);
  const [is_allow, setIsAllow] = useState(false);
  const [verified, setVerified] = useState(false);
  const [walpublic, setWalPublic] = useState('');
  const [walprivate, setWalPrivate] = useState('');
  const [btcwalpublic, setBTCWalPublic] = useState('');
  const [btcwalprivate, setBTCWalPrivate] = useState('');
  const [coin_btc, setCoinBTC] = useState(false);
  const [coin_eth, setCoinETH] = useState(false);
  const [coin_bnb, setCoinBNB] = useState(false);
  const [coin_xrp, setCoinXRP] = useState(false);
  const [coin_usdt, setCoinUSDT] = useState(false);
  const [coin_cpos, setCoinCPOS] = useState(false);

  const handleETHWallet = async () => {
    const ret = await userService.generateWalletDetails(1);    
    setWalPublic(ret.data.public);
    setWalPrivate(ret.data.private);
  }

  const handleBTCWallet = async () => {
    const ret = await userService.generateWalletDetails(0);
    setBTCWalPublic(ret.data.public);
    setBTCWalPrivate(ret.data.private);
  }

  const handleConfirm = () => {  
    dispatch(adminUpdateUserData(seluser._id, {
      businessname,
      address,
      postcode,
      telephone,
      notes,
      active,
      is_admin,
      is_allow,
      verified,
      walpublic,
      walprivate,
      btcwalpublic,
      btcwalprivate,
      coin_btc,
      coin_eth,
      coin_bnb,
      coin_xrp,
      coin_usdt,
      coin_cpos
    }));
    setIsModal(false);
  }

  useEffect(() => {
    if (isModal && seluser) {
      setBusinessname(seluser.businessname);
      // setPassword(seluser.password);
      setAddress(seluser.address);
      setPostcode(seluser.postcode);
      setTelephone(seluser.telephone);
      setNotes(seluser.notes);
      setActive(seluser.active);
      setIsAdmin(seluser.is_admin);
      setIsAllow(seluser.is_allow);
      setVerified(seluser.verified);
      setWalPublic(seluser.walpublic);
      setWalPrivate(seluser.walprivate);
      setBTCWalPublic(seluser.btcwalpublic);
      setBTCWalPrivate(seluser.btcwalprivate);
      setCoinBTC(seluser.coin_btc);
      setCoinETH(seluser.coin_eth);
      setCoinBNB(seluser.coin_bnb);
      setCoinXRP(seluser.coin_xrp);
      setCoinUSDT(seluser.coin_usdt);
      setCoinCPOS(seluser.coin_cpos);
    }
  }, [seluser._id, isModal])

  return (
    <Transition appear show={isModal} as={Fragment}>
      <Dialog
        as="div"
        onClose={() => {}}
        className="relative z-50">
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          <div className="fixed inset-0 backdrop-blur-sm bg-[#FFFFFF44]" aria-hidden="true" />
        </Transition.Child>
        <div className="fixed inset-0 flex items-center bg-transparent justify-center ">
          {/* The actual dialog panel  */}
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95">
            <Dialog.Panel className=" max-w-full transform transition-all md:max-w-full" onKeyUp={(e) => {if (e.code === 'Enter' || e.code === 'NumpadEnter') { handleConfirm()}} }>
              <div className="relative bg-[#404040] w-[50vw] max-w-[1000px] min-w-[380px] min-h-[160px] rounded-[1rem] drop-shadow-md p-6 text-center shadow-xl">                              
                {/* TITLE */}
                <div className='mb-2 flex justify-between'>
                  <h1 className='text-xl text-left text-[#D9D9D9] select-none'>Edit User</h1>
                  <h1 className='text-xl text-left text-[#D9D9D9]'>{seluser._id}</h1>
                </div>
                {/* CONTENTS */}
                <div className="w-full min-h-[26rem] pb-[0.5rem]">
                  <div className="w-full flex">
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Business Name:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={businessname}
                          onChange={e => setBusinessname(e.target.value)} 
                      />
                    </div>
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Password:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="password"
                          value={seluser.password}
                          onChange={e => {}}
                      />
                    </div>
                  </div>
                  <div className="w-full flex">
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Email:
                      </label>
                      <input 
                          className="appearance-none bg-transparent w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={seluser.email}
                          readOnly
                      />
                    </div>
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Address:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={address}
                          onChange={e => setAddress(e.target.value)}
                      />
                    </div>
                  </div>
                  <div className="w-full flex">
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Post Code:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={postcode}
                          onChange={e => setPostcode(e.target.value)} 
                      />
                    </div>
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Role:
                      </label>
                      <input 
                          className="appearance-none bg-transparent w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={seluser.role}
                          readOnly
                      />
                    </div>
                  </div>
                  <div className="w-full flex">
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Telephone:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={telephone}
                          onChange={e => setTelephone(e.target.value)} 
                      />
                    </div>
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Notes:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={notes}
                          onChange={e => setNotes(e.target.value)}
                      />
                    </div>
                  </div>
                  <div className="w-full flex">
                    <div className="flex flex-row items-center py-1 w-[25%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[6rem] text-left">
                        Active:
                      </label>
                      <Switch onChange={()=>{setActive(!active)}} checked={active} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                    <div className="flex flex-row items-center py-1 w-[25%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[6rem] text-left">
                        IsAdmin:
                      </label>
                      <Switch onChange={()=>{setIsAdmin(!is_admin)}} checked={is_admin} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                    <div className="flex flex-row items-center py-1 w-[25%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[6rem] text-left">
                        IsAllow:
                      </label>
                      <Switch onChange={()=>{setIsAllow(!is_allow)}} checked={is_allow} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                    <div className="flex flex-row items-center py-1 w-[25%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[6rem] text-left">
                        Verified:
                      </label>
                      <Switch onChange={()=>{setVerified(!verified)}} checked={verified} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                  </div>
                  <div className="w-full flex">
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Wallet Public:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={walpublic}
                          onChange={e => setWalPublic(e.target.value)} 
                      />
                    </div>
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        Wallet Private:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={walprivate}
                          onChange={e => setWalPrivate(e.target.value)} 
                      />
                    </div>
                  </div>
                  <div className="w-full flex">
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        BTC Wallet Public:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={btcwalpublic}
                          onChange={e => setBTCWalPublic(e.target.value)} 
                      />
                    </div>
                    <div className="flex flex-row items-center py-1 w-[50%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[10rem] text-left">
                        BTC Wallet Private:
                      </label>
                      <input 
                          className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 px-2 focus:outline-none" 
                          type="text"
                          value={btcwalprivate}
                          onChange={e => setBTCWalPrivate(e.target.value)} 
                      />
                    </div>
                  </div>
                  <div className="w-full flex">
                    <div className="flex flex-row items-center py-1 w-[15%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[3rem] text-left">
                        BTC:
                      </label>
                      <Switch onChange={()=>{setCoinBTC(!coin_btc)}} checked={coin_btc} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                    <div className="flex flex-row items-center py-1 w-[15%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[3rem] text-left">
                        ETH:
                      </label>
                      <Switch onChange={()=>{setCoinETH(!coin_eth)}} checked={coin_eth} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                    <div className="flex flex-row items-center py-1 w-[15%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[3rem] text-left">
                        BNB:
                      </label>
                      <Switch onChange={()=>{setCoinBNB(!coin_bnb)}} checked={coin_bnb} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                    <div className="flex flex-row items-center py-1 w-[15%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[3rem] text-left">
                        XRP:
                      </label>
                      <Switch onChange={()=>{setCoinXRP(!coin_xrp)}} checked={coin_xrp} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                    <div className="flex flex-row items-center py-1 w-[15%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[3rem] text-left">
                        USDT:
                      </label>
                      <Switch onChange={()=>{setCoinUSDT(!coin_usdt)}} checked={coin_usdt} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                    <div className="flex flex-row items-center py-1 w-[15%]">
                      <label className="block uppercase tracking-wide text-gray-100 mb-1 w-[3rem] text-left">
                        CPOS:
                      </label>
                      <Switch onChange={()=>{setCoinCPOS(!coin_cpos)}} checked={coin_cpos} offColor={'#800'} height={18} width={36} handleDiameter={18} checkedIcon={false} uncheckedIcon={false}/>
                    </div>
                  </div>
                </div>                
                {/* CONTROLS */}
                <div className='absolute right-6 bottom-4'>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#404040] border border-[#D9D9D9] text-gray-100 mr-6`}            
                      onClick={() => setIsModal(false)}
                  >Cancel</button>
                  <button 
                      className={`w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}            
                      onClick={() => handleConfirm()}
                  >Confirm</button>
                </div>   
                <div className='absolute left-6 bottom-4'>
                  <button 
                      className={`w-[9rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800 mr-6`}            
                      onClick={() => handleETHWallet()}
                  >Get EVM Wallet</button>
                  <button 
                      className={`w-[9rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}            
                      onClick={() => handleBTCWallet()}
                  >Get BTC Wallet</button>
                </div>       
              </div>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition>
  );
};

export default AdminEditUserModal;