import { useEffect, useState, createRef } from 'react';
import Layout from '../../components/Layout';
import { useSelector, useDispatch } from 'react-redux';
import { API_IMAGE_PATH, COIN_TYPES } from '../../config/constants';
import { selectAllCategories, getAllCategories } from '../../redux/pcategoryReducer';
import { selectAllProducts, getAllProducts } from '../../redux/productReducer';
import { selectOrders, getOrders } from '../../redux/orderReducer';
import { selectTotalUsers, getTotalUserData } from '../../redux/userReducer';
import { floatMul } from '../../utils/utils';
import AdminEditUserModal from '../admin_modals/edit.user.modal';
import AdminEditCategoryModal from '../admin_modals/edit.category.modal';
import AdminEditProductModal from '../admin_modals/edit.product.modal';
import AdminEditOrderModal from '../admin_modals/edit.order.modal';
import { openSnackBar } from '../../redux/snackBarReducer';

const Administrator = () => {
  const dispatch = useDispatch();
  const [widget, setWidget] = useState('users');
  const categories = useSelector(selectAllCategories);
  const products = useSelector(selectAllProducts);  
  const orders = useSelector(selectOrders); 
  const users = useSelector(selectTotalUsers);

  const [userModal, setUserModal] = useState(false);
  const [seluser, setSelUser] = useState(null);
  const [categoryModal, setCategoryModal] = useState(false);
  const [selcategory, setSelCategory] = useState(null);
  const [productModal, setProductModal] = useState(false);
  const [selproduct, setSelProduct] = useState(null);
  const [orderModal, setOrderModal] = useState(false);
  const [selorder, setSelOrder] = useState(null);

  const categoryName = (id) => {
    const cate = categories.filter((c) => c._id === id);
    return cate[0] ? cate[0].name : '';
  }

  const getProduct = (id) => {
    const p = products.filter((e) => e._id === id);
    return p[0];
  }

  const form_groups = [
    <div key={1}>
      <div className='h-full'>
        <div className='w-full flex text-gray-100 leading-[1.8rem]'>
          <p className='w-[10%] pl-6'>BusinessName</p>
          <p className='w-[15%] text-center'>Email</p>
          <p className='w-[5%] text-center'>Role</p>
          <p className='w-[15%] text-center'>Address</p>
          <p className='w-[10%] text-center'>PostCode</p>
          <p className='w-[5%] text-center'>Telephone</p>
          <p className='w-[5%] text-center'>Active</p>
          <p className='w-[5%] text-center'>IsAdmin</p>
          <p className='w-[5%] text-center'>IsAllow</p>
          <p className='w-[5%] text-center'>Verified</p>
          <p className='w-[10%] text-center'>WalletPublic</p>
          <p className='w-[10%] text-center'>WalletPrivate</p>
        </div>
        <div className='h-[calc(100vh-180px)] overflow-y-auto'>
        {
          users.map((user, index) => {
            const userproxyes = user.userproxy;
            const usercustomers = user.usercustomer;
            return (
              <div key={index}>
                <div className='w-full flex text-gray-100 leading-[1.4rem] bg-[#333333] border-b border-[#282828] py-1'>
                  <p className='w-[10%] pl-6 cursor-pointer hover:text-blue-300' onClick={()=>handleAdminEditUserModal(user)}>{user.businessname}</p>
                  <p className='w-[15%] text-center'>{user.email}</p>
                  <p className='w-[5%] text-center'>{user.role}</p>
                  <p className='w-[15%] text-center'>{user.address}</p>
                  <p className='w-[10%] text-center'>{user.postcode}</p>
                  <p className='w-[5%] text-center'>{user.telephone}</p>
                  <p className='w-[5%] text-center'>{user.active?'True':'False'}</p>
                  <p className='w-[5%] text-center'>{user.is_admin?'True':'False'}</p>
                  <p className='w-[5%] text-center'>{user.is_allow?'True':'False'}</p>
                  <p className='w-[5%] text-center'>{user.verified?'True':'False'}</p>
                  <p className='w-[10%] text-center'>
                    <p className="cursor-pointer hover:text-blue-300" onClick={()=>handleClipboard(user.walpublic)}>{user.walpublic.substr(0, 6) + "..." + user.walpublic.substr(user.walpublic.length - 4, 4)}</p>
                    <p className="cursor-pointer hover:text-blue-300" onClick={()=>handleClipboard(user.btcwalpublic)}>{user.btcwalpublic.substr(0, 6) + "..." + user.btcwalpublic.substr(user.btcwalpublic.length - 4, 4)}</p>
                  </p>
                  <p className='w-[10%] text-center'>
                    <p className="cursor-pointer hover:text-blue-300" onClick={()=>handleClipboard(user.walprivate)}>{user.walprivate.substr(0, 6) + "..." + user.walprivate.substr(user.walprivate.length - 4, 4)}</p>
                    <p className="cursor-pointer hover:text-blue-300" onClick={()=>handleClipboard(user.btcwalprivate)}>{user.btcwalprivate.substr(0, 6) + "..." + user.btcwalprivate.substr(user.btcwalprivate.length - 4, 4)}</p>
                  </p>
                </div>
                {/* <div className='w-full flex text-gray-100 leading-[1.4rem]'>
                  {
                    userproxyes.map((userproxy, index) => {
                      return (
                        <div key={index}>
                          <div className='w-full flex text-gray-100 leading-[1.4rem]'>
                            <p className='w-[10%] pl-16 text-red'>{userproxy.username}</p>
                          </div>
                        </div>
                      )
                    })
                  }
                </div> */}
                <div className='w-full flex flex-col text-gray-100 leading-[1.4rem]'>
                  {
                    usercustomers.map((usercustomer, index) => {
                      return (
                        <div key={index}>
                          <div className='w-full flex text-gray-100 leading-[1.4rem]'>
                            <p className='w-[10%] pl-16 text-red'>{usercustomer.name}</p>
                            <p className='w-[10%] pl-16 text-red'>{usercustomer.email}</p>
                            <p className='w-[10%] pl-16 text-red'>{usercustomer.telephone}</p>
                            <p className='w-[10%] pl-16 text-red'>{usercustomer.address}</p>
                            <p className='w-[10%] pl-16 text-red'>{usercustomer.postcode}</p>
                            {/* <p className='w-[10%] pl-16 text-red'>{usercustomer.notes}</p> */}
                          </div>
                        </div>
                      )
                    })
                  }
                </div>
              </div>
            )
          })
        }
        </div>
      </div>
    </div>,
    <div key={2}>
      <div className='w-full flex text-gray-100 leading-[1.8rem]'>
          <p className='w-[70%] pl-6'>Name</p>
          <p className='w-[30%] text-center'>CreatedBy</p>
      </div>
      <div className='h-[calc(100vh-180px)] overflow-y-auto'>
        {
          categories.map((c, index) => {
            return (
              <div key={index} className='w-full flex text-gray-100 leading-[1.4rem] bg-[#333333] border-b border-[#282828] py-1'>
                <p className='w-[70%] pl-6 cursor-pointer hover:text-blue-300' onClick={()=>handleAdminEditCategoryModal(c)}>{c.name}</p>
                <p className='w-[30%] text-center'>{c.createdBy}</p>
              </div>
            )
          })
        }
      </div>
    </div>,
    <div key={3}>
      <div className='h-full overflow-y-auto'>
        <div className='w-full flex text-gray-100 leading-[1.8rem]'>
            <p className='w-[15%] pl-6'>Name</p>
            <p className='w-[20%] text-center'>Description</p>
            <p className='w-[15%] text-center'>Category</p>
            <p className='w-[15%] text-center'>SKU</p>
            <p className='w-[10%] text-center'>Photo</p>
            <p className='w-[10%] text-center'>CreatedBy</p>
            <p className='w-[15%] text-center'>Price</p>
        </div>
        <div className='h-[calc(100vh-180px)] overflow-y-auto'>
        {
          products.map((p, index) => {
            return (
              <div key={index}>
                <div className='w-full flex text-gray-100 leading-[1.4rem] bg-[#333333] border-b border-[#282828] py-1'>
                  <p className='w-[15%] pl-6 cursor-pointer hover:text-blue-300' onClick={()=>handleAdminEditProductModal(p)}>{p.name}</p>
                  <p className='w-[20%] text-center'>{p.description}</p>
                  <p className='w-[15%] text-center'>{categoryName(p.categoryId)}</p>
                  <p className='w-[15%] text-center'>{p.sku}</p>
                  <p className='w-[10%] text-center'>{p.photo}</p>
                  <p className='w-[10%] text-center'>{p.createdBy}</p>
                  <p className='w-[15%] text-center'>{p.price}</p>
                </div>
              </div>
            )
          })
        }
        </div>
      </div>
    </div>,
    <div key={4}>
      <div className='h-full overflow-y-auto'>
        <div className='w-full flex text-gray-100 leading-[1.8rem]'>
          <p className='w-[8%] pl-6'>Name</p>
          <p className='w-[14%] text-center'>Merchant</p>
          <p className='w-[14%] text-center'>Customer</p>
          <p className='w-[16%] text-center'>OrderList</p>
          <p className='w-[5%] text-center'>Payment</p>
          <p className='w-[5%] text-center'>Refund</p>
          <p className='w-[6%] text-center'>Waiting</p>
          <p className='w-[8%] text-center'>TransactionID</p>
          <p className='w-[16%] text-center'>CoinAmount</p>
          <p className='w-[8%] text-center'>CoinType</p>
        </div>
        <div className='h-[calc(100vh-180px)] overflow-y-auto'>
        {
          orders.map((order, index) => {
            const orderlist = JSON.parse(order.order.order);
            return (
              <div key={index}>
                <div className='w-full flex text-gray-100 leading-[1.4rem] bg-[#333333] border-b border-[#282828] py-1'>
                  <p className='w-[8%] pl-6 cursor-pointer hover:text-blue-300' onClick={()=>handleAdminEditOrderModal(order)}>{order.order.name}</p>
                  <p className='w-[14%] text-center'>{order.merchant.email}</p>
                  <p className='w-[14%] text-center'>{order.customer ? order.customer.email:'None'}</p>
                  <p className='w-[16%]'>
                    {
                      orderlist.map((o, i) => {
                        const p = getProduct(o.productId);
                        return (
                          <div key={i+20000*index}>
                            {p && <div className="py-1">    
                                <div className='flex justify-between text-md'>
                                    <div>{p.name} X {o.amount}</div>
                                    <div>{floatMul(p.price, o.amount).toFixed(2)}</div>
                                </div>
                            </div>}
                          </div>
                        );
                      })
                    }
                  </p>
                  <p className='w-[5%] text-center'>{order.order.payment ? 'True':'False'}</p>
                  <p className='w-[5%] text-center'>{order.order.refund ? 'True':'False'}</p>
                  <p className='w-[6%] text-center'>{order.order.waiting ? 'True':'False'}</p>
                  <p className='w-[8%] text-center'>{order.order.transactionId}</p>
                  <p className='w-[16%] text-center'>{order.order.coinAmount}</p>
                  <p className='w-[8%] text-center'>{COIN_TYPES[order.order.coinType]}</p>
                </div>
              </div>
            )
          })
        }
        </div>
      </div>
    </div>
  ]

  const refs = form_groups.reduce((acc, val, i) => {
    acc[i] = createRef();
    return acc;
  }, {});

  const totalGroups = form_groups.length;
  const scrollToGroup = i => {
    refs[i].current.scrollIntoView({
      behavior: 'smooth',
      block: 'nearest',
      inline: 'start',
    });
  };

  useEffect(() => {
    dispatch(getTotalUserData());
    dispatch(getAllCategories());
    dispatch(getAllProducts());
    dispatch(getOrders());
  }, [])

  const handleAdminEditUserModal = (user) => {
    setSelUser(user);
    setUserModal(true);
  }

  const handleClipboard = (address) => {
    navigator.clipboard.writeText(address);
    dispatch(openSnackBar({ message: "Copied to clipboard successfully", status: 'success' }));
  }

  const handleAdminEditCategoryModal = (category) => {
    setSelCategory(category);
    setCategoryModal(true);
  }

  const handleAdminEditProductModal = (product) => {
    setSelProduct(product);
    setProductModal(true);
  }

  const handleAdminEditOrderModal = (order) => {
    setSelOrder(order);
    setOrderModal(true);
  }

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full">
              <div className='flex border-b border-gray-300'>
                  <button 
                      className={`w-full text-xl leading-[3.6rem] rounded-t-[1rem] ${widget === 'users' ? 'bg-[#D9D9D9] text-gray-800' : 'bg-transparent text-gray-100'}`}
                      onClick={() => {setWidget('users');scrollToGroup(0);}}
                  >Users</button>
                  <button 
                      className={`w-full text-xl leading-[3.6rem] rounded-t-[1rem] ${widget === 'category' ? 'bg-[#D9D9D9] text-gray-800' : 'bg-transparent text-gray-100'}`}
                      onClick={() => {setWidget('category');scrollToGroup(1);}}
                  >Category</button>
                  <button 
                      className={`w-full text-xl leading-[3.6rem] rounded-t-[1rem] ${widget === 'products' ? 'bg-[#D9D9D9] text-gray-800' : 'bg-transparent text-gray-100'}`}
                      onClick={() => {setWidget('products');scrollToGroup(2);}}
                  >Products</button>
                  <button 
                      className={`w-full text-xl leading-[3.6rem] rounded-t-[1rem] ${widget === 'orders' ? 'bg-[#D9D9D9] text-gray-800' : 'bg-transparent text-gray-100'}`}
                      onClick={() => {setWidget('orders');scrollToGroup(3);}}
                  >Orders</button>
              </div>
            </div>
        </div>          
        <div className='w-full h-[calc(100vh-140px)]'>
            <div className="carousel w-full h-full">
                {form_groups.map((form_group, i) => {
                    return (
                        <div className='w-full h-full flex-shrink-0' key={i} ref={refs[i]}>  
                            {form_group} 
                        </div>
                    )
                })}
            </div>
        </div>  
        {seluser && <AdminEditUserModal isModal={userModal} setIsModal={setUserModal} seluser={seluser}/>}
        {selcategory && <AdminEditCategoryModal isModal={categoryModal} setIsModal={setCategoryModal} selcategory={selcategory}/>}
        {selproduct && <AdminEditProductModal isModal={productModal} setIsModal={setProductModal} selproduct={selproduct}/>}
        {selorder && <AdminEditOrderModal isModal={orderModal} setIsModal={setOrderModal} selorder={selorder}/>}
    </Layout>
  );
}

export default Administrator;